// src/pages/Books.jsx - with Barcode Scanner Integration
import React, { useState, useEffect } from 'react';
import {
  Table,
  Button,
  Input,
  Card,
  Space,
  Modal,
  Form,
  Select,
  DatePicker,
  InputNumber,
  Typography,
  Tag,
  Tooltip,
  Popconfirm,
  Divider,
  message,
} from 'antd';
import {
  SearchOutlined,
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  BookOutlined,
} from '@ant-design/icons';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { useAppContext } from '../context/AppContext';
import BarcodeScanner from '../components/common/BarcodeScanner';
import dayjs from 'dayjs';

const { Title, Text } = Typography;
const { Option } = Select;
const { TextArea } = Input;

// Style for RTL tables
const StyledTable = styled(Table)`
  .ant-table-thead > tr > th {
    text-align: right;
  }

  .ant-table-cell {
    text-align: right;
  }
`;

const StyledCard = styled(Card)`
  margin-bottom: 24px;
`;

const FilterContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 24px;
`;

const ActionsColumn = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 8px;
`;

const ISBNFieldContainer = styled.div`
  display: flex;
  gap: 8px;
`;

const Books = () => {
  const {
    books,
    addBook,
    updateBook,
    deleteBook,
    authors,
    categories,
    publishers,
    createBookModel,
  } = useAppContext();

  const location = useLocation();
  const navigate = useNavigate();

  // State
  const [form] = Form.useForm();
  const [searchText, setSearchText] = useState('');
  const [filters, setFilters] = useState({
    categories: [],
    authors: [],
    publishers: [],
  });
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [editingBook, setEditingBook] = useState(null);
  const [filteredBooks, setFilteredBooks] = useState([]);

  // Check for edit parameter in URL
  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const editId = queryParams.get('edit');

    if (editId) {
      const bookToEdit = books.find((book) => book.id === editId);
      if (bookToEdit) {
        showModal(bookToEdit);
        // Remove the query parameter
        navigate('/books', { replace: true });
      }
    }
  }, [location, books, navigate]);

  // Apply filters and search
  useEffect(() => {
    let result = [...books];

    // Apply search
    if (searchText) {
      const lowerSearchText = searchText.toLowerCase();
      result = result.filter(
        (book) =>
          book.title.toLowerCase().includes(lowerSearchText) ||
          getAuthorName(book.author).toLowerCase().includes(lowerSearchText) ||
          book.isbn?.toLowerCase().includes(lowerSearchText) ||
          getPublisherName(book.publisher)
            .toLowerCase()
            .includes(lowerSearchText)
      );
    }

    // Apply category filter
    if (filters.categories.length > 0) {
      result = result.filter((book) =>
        book.categories?.some((catId) => filters.categories.includes(catId))
      );
    }

    // Apply author filter
    if (filters.authors.length > 0) {
      result = result.filter((book) => filters.authors.includes(book.author));
    }

    // Apply publisher filter
    if (filters.publishers.length > 0) {
      result = result.filter((book) =>
        filters.publishers.includes(book.publisher)
      );
    }

    setFilteredBooks(result);
  }, [books, searchText, filters]);

  // Helper functions
  const getAuthorName = (authorId) => {
    const author = authors.find((a) => a.id === authorId);
    return author ? author.name : ' ';
  };

  const getPublisherName = (publisherId) => {
    const publisher = publishers.find((p) => p.id === publisherId);
    return publisher ? publisher.name : ' ';
  };

  const getCategoryNames = (categoryIds) => {
    if (!categoryIds || !categoryIds.length) return [];

    return categoryIds
      .map((id) => {
        const category = categories.find((c) => c.id === id);
        return category
          ? {
              name: category.name,
              color: category.color,
            }
          : null;
      })
      .filter(Boolean);
  };

  // Modal handlers
  const showModal = (book = null) => {
    setEditingBook(book);

    if (book) {
      form.setFieldsValue({
        ...book,
        publicationYear: book.publicationYear
          ? dayjs(book.publicationYear.toString())
          : null,
        acquisitionDate: book.acquisitionDate
          ? dayjs(book.acquisitionDate)
          : null,
      });
    } else {
      form.resetFields();
    }

    setIsModalVisible(true);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    form.resetFields();
  };

  const handleSubmit = async (values) => {
    // Format dates
    const formattedValues = {
      ...values,
      publicationYear: values.publicationYear
        ? values.publicationYear.year()
        : null,
      acquisitionDate: values.acquisitionDate
        ? values.acquisitionDate.toISOString()
        : null,
    };

    if (editingBook) {
      await updateBook(editingBook.id, formattedValues);
      message.success('  ');
    } else {
      await addBook(formattedValues);
      message.success('  ');
    }

    setIsModalVisible(false);
    form.resetFields();
  };

  const handleDelete = async (bookId) => {
    await deleteBook(bookId);
    message.success('  ');
  };

  // Handle barcode scan results
  const handleBarcodeScan = (scanData) => {
    console.log('Barcode scan result:', scanData);

    // Update the ISBN field
    form.setFieldValue('isbn', scanData.isbn);

    // If we have more data from online, fill in other fields
    if (scanData.title) {
      form.setFieldValue('title', scanData.title);
    }

    if (scanData.pageCount) {
      form.setFieldValue('pageCount', scanData.pageCount);
    }

    if (scanData.publishDate) {
      // Try to extract year from publish date
      const yearMatch = scanData.publishDate.match(/\d{4}/);
      if (yearMatch) {
        form.setFieldValue('publicationYear', dayjs(yearMatch[0]));
      }
    }

    // If we have author information, try to find or create the author
    if (scanData.authors) {
      // This is a simplified approach - in a real app, you might want to
      // show a dialog asking the user to select an existing author or create a new one
      const existingAuthor = authors.find(
        (author) => author.name.toLowerCase() === scanData.authors.toLowerCase()
      );

      if (existingAuthor) {
        form.setFieldValue('author', existingAuthor.id);
      } else {
        // Here you could show a dialog to create a new author
        message.info(
          ` "${scanData.authors}"   .      .`
        );
      }
    }

    // Same for publisher
    if (scanData.publisher) {
      const existingPublisher = publishers.find(
        (publisher) =>
          publisher.name.toLowerCase() === scanData.publisher.toLowerCase()
      );

      if (existingPublisher) {
        form.setFieldValue('publisher', existingPublisher.id);
      } else {
        message.info(
          `  "${scanData.publisher}"   .       .`
        );
      }
    }

    // Display success message
    message.success('   ');
  };

  // Table columns
  const columns = [
    {
      title: ' ',
      dataIndex: 'title',
      key: 'title',
      render: (text, record) => (
        <Link to={`/books/${record.id}`}>
          <Text strong>{text}</Text>
        </Link>
      ),
    },
    {
      title: '',
      dataIndex: 'author',
      key: 'author',
      render: (authorId) => getAuthorName(authorId),
    },
    {
      title: ' ',
      dataIndex: 'publisher',
      key: 'publisher',
      render: (publisherId) => getPublisherName(publisherId),
    },
    {
      title: '',
      dataIndex: 'categories',
      key: 'categories',
      render: (categoryIds) => {
        const cats = getCategoryNames(categoryIds);
        return (
          <Space size={[0, 4]} wrap>
            {cats.length > 0 ? (
              cats.map((cat, index) => (
                <Tag color={cat.color} key={index}>
                  {cat.name}
                </Tag>
              ))
            ) : (
              <Text type="secondary">-</Text>
            )}
          </Space>
        );
      },
    },
    {
      title: ' ',
      dataIndex: 'publicationYear',
      key: 'publicationYear',
      render: (year) => year || '-',
    },
    {
      title: '',
      key: 'actions',
      width: 120,
      render: (_, record) => (
        <ActionsColumn>
          <Tooltip title="">
            <Button
              icon={<EditOutlined />}
              type="text"
              onClick={() => showModal(record)}
            />
          </Tooltip>
          <Tooltip title="">
            <Popconfirm
              title="     ?"
              onConfirm={() => handleDelete(record.id)}
              okText=""
              cancelText=""
              placement="topRight"
            >
              <Button icon={<DeleteOutlined />} type="text" danger />
            </Popconfirm>
          </Tooltip>
        </ActionsColumn>
      ),
    },
  ];

  return (
    <div>
      <StyledCard>
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: 16,
          }}
        >
          <Title level={2}></Title>
          <Button
            type="primary"
            icon={<PlusOutlined />}
            onClick={() => showModal()}
          >
             
          </Button>
        </div>

        <FilterContainer>
          <Input
            placeholder="  ,   "
            prefix={<SearchOutlined />}
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
            style={{ width: 300 }}
          />
          <Select
            mode="multiple"
            allowClear
            style={{ minWidth: 200 }}
            placeholder="  "
            value={filters.categories}
            onChange={(values) =>
              setFilters({ ...filters, categories: values })
            }
          >
            {categories.map((category) => (
              <Option key={category.id} value={category.id}>
                {category.name}
              </Option>
            ))}
          </Select>
          <Select
            mode="multiple"
            allowClear
            style={{ minWidth: 200 }}
            placeholder="  "
            value={filters.authors}
            onChange={(values) => setFilters({ ...filters, authors: values })}
          >
            {authors.map((author) => (
              <Option key={author.id} value={author.id}>
                {author.name}
              </Option>
            ))}
          </Select>
          <Select
            mode="multiple"
            allowClear
            style={{ minWidth: 200 }}
            placeholder="   "
            value={filters.publishers}
            onChange={(values) =>
              setFilters({ ...filters, publishers: values })
            }
          >
            {publishers.map((publisher) => (
              <Option key={publisher.id} value={publisher.id}>
                {publisher.name}
              </Option>
            ))}
          </Select>
        </FilterContainer>

        <StyledTable
          dataSource={filteredBooks}
          columns={columns}
          rowKey="id"
          pagination={{
            pageSize: 10,
            showSizeChanger: true,
            showTotal: (total) => `" ${total} `,
          }}
          locale={{
            emptyText: '  ',
          }}
        />
      </StyledCard>

      {/* Book Form Modal */}
      <Modal
        title={editingBook ? ' ' : '  '}
        open={isModalVisible}
        onCancel={handleCancel}
        footer={null}
        destroyOnClose
        width={700}
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSubmit}
          initialValues={createBookModel()}
        >
          <Form.Item
            name="title"
            label=" "
            rules={[{ required: true, message: '    ' }]}
          >
            <Input placeholder="   " />
          </Form.Item>

          <Form.Item
            name="author"
            label=""
            rules={[{ required: true, message: '  ' }]}
          >
            <Select
              placeholder=" "
              showSearch
              optionFilterProp="children"
              allowClear
              dropdownRender={(menu) => (
                <>
                  {menu}
                  <Divider style={{ margin: '8px 0' }} />
                  <Button
                    type="link"
                    block
                    icon={<PlusOutlined />}
                    onClick={() => navigate('/authors')}
                  >
                      
                  </Button>
                </>
              )}
            >
              {authors.map((author) => (
                <Option key={author.id} value={author.id}>
                  {author.name}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item name="publisher" label=" ">
            <Select
              placeholder="  "
              showSearch
              optionFilterProp="children"
              allowClear
              dropdownRender={(menu) => (
                <>
                  {menu}
                  <Divider style={{ margin: '8px 0' }} />
                  <Button
                    type="link"
                    block
                    icon={<PlusOutlined />}
                    onClick={() => navigate('/publishers')}
                  >
                       
                  </Button>
                </>
              )}
            >
              {publishers.map((publisher) => (
                <Option key={publisher.id} value={publisher.id}>
                  {publisher.name}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item name="categories" label="">
            <Select
              mode="multiple"
              placeholder=" "
              showSearch
              optionFilterProp="children"
              allowClear
              dropdownRender={(menu) => (
                <>
                  {menu}
                  <Divider style={{ margin: '8px 0' }} />
                  <Button
                    type="link"
                    block
                    icon={<PlusOutlined />}
                    onClick={() => navigate('/categories')}
                  >
                      
                  </Button>
                </>
              )}
            >
              {categories.map((category) => (
                <Option key={category.id} value={category.id}>
                  {category.name}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item name="isbn" label=" (ISBN)">
            <ISBNFieldContainer>
              <Input placeholder=" " style={{ flex: 1 }} />
              <BarcodeScanner onScan={handleBarcodeScan} buttonText="" />
            </ISBNFieldContainer>
          </Form.Item>

          <div style={{ display: 'flex', gap: 16 }}>
            <Form.Item
              name="publicationYear"
              label=" "
              style={{ flex: 1 }}
            >
              <DatePicker picker="year" style={{ width: '100%' }} />
            </Form.Item>

            <Form.Item name="language" label="" style={{ flex: 1 }}>
              <Select placeholder=" " defaultValue="Hebrew">
                <Option value="Hebrew"></Option>
                <Option value="English"></Option>
                <Option value="Arabic"></Option>
                <Option value="Russian"></Option>
                <Option value="French"></Option>
                <Option value="German"></Option>
                <Option value="Spanish"></Option>
                <Option value="Other"></Option>
              </Select>
            </Form.Item>
          </div>

          <div style={{ display: 'flex', gap: 16 }}>
            <Form.Item name="pageCount" label=" " style={{ flex: 1 }}>
              <InputNumber
                placeholder="  "
                style={{ width: '100%' }}
              />
            </Form.Item>

            <Form.Item name="rating" label="" style={{ flex: 1 }}>
              <InputNumber
                placeholder=" 1-5"
                min={1}
                max={5}
                style={{ width: '100%' }}
              />
            </Form.Item>
          </div>

          <Form.Item name="description" label=" ">
            <TextArea placeholder=" " rows={4} />
          </Form.Item>

          <Form.Item name="location" label=" ">
            <Input placeholder="   (, ,  ')" />
          </Form.Item>

          <Form.Item name="acquisitionDate" label=" ">
            <DatePicker style={{ width: '100%' }} />
          </Form.Item>

          <Form.Item name="acquisitionMethod" label=" ">
            <Select placeholder="  ">
              <Option value="purchase"></Option>
              <Option value="gift"></Option>
              <Option value="donation"></Option>
              <Option value="other"></Option>
            </Select>
          </Form.Item>

          <Form.Item name="notes" label="">
            <TextArea placeholder=" " rows={3} />
          </Form.Item>

          {/* Footer */}
          <div style={{ display: 'flex', justifyContent: 'flex-end', gap: 16 }}>
            <Button onClick={handleCancel}></Button>
            <Button type="primary" htmlType="submit">
              {editingBook ? '' : ''}
            </Button>
          </div>
        </Form>
      </Modal>
    </div>
  );
};

export default Books;
